// 提示组件
import tips from '../../element/tips.vue'
// post请求
import {publicing} from '../../api/api.js'
// 地址
import {wxpayingurl} from '../../api/request.js'
var {log} = console
export default{
	components:{tips},
	data() {
		return {
			openid:'',
			neworder:[]
		}
	},
	
	
	methods:{
		// 反馈提示
		tising(bull,tips){
			this.$nextTick(()=>{
				this.$refs.tips.init(bull,tips)
			})
		},
		
		// 请求数据
		orderIng(){
			let data = {
				type:'myorder',
				openid:this.openid
			}
			publicing(wxpayingurl,data)
			.then((res)=>{
				log(res)
				if(res.data === 0){
					let bull = true
					let tips = '你还没有订单'
					this.tising(bull,tips)
				}else{
					let bull = false
					let tips = ''
					this.tising(bull,tips)
					// 筛选需要的数据
					let neworder = res.data.map((item)=>{
						let merchantid = item._merchantid
						let openid = item._openid
						let status = item.status
						let body = item.wxorder.body
						let logo = item.wxorder.logo
						let total_fee = item.wxorder.total_fee
						let Paymentinfor = item.wxorder.Paymentinfor.arrinfo
						return{
							merchantid,
							openid,
							status,
							body,
							logo,
							total_fee,
							Paymentinfor
						}
					})
					log(neworder)
					this.neworder = neworder
				}
			})
			.catch((err)=>{
				log(err)
			})
		},
		
		// 去评价
		evaLuate(ide,body){
			wx.navigateTo({
				url:'../evaluate/evaluate?ide=' + ide + '&body=' + body
			})
		}
	},
	
	
	
	
	
	
	
	// 看订单的前提条件就是是否登录
	onShow() {
		let setdata = uni.getStorageSync('usermen')
		if(!setdata){
			let bull = true
			let tips = '请登录后再查看'
			this.tising(bull,tips)
		}else{
			this.openid = setdata.openid
			let bull = false
			let tips = ''
			this.tising(bull,tips)
			this.orderIng()
		}	
	}
	
	
	
	
}
